<script>

export default {
  name:'Payment',
  data(){
     return{
       price: "32.750,15",
       warning:""
     }
   },
  methods: {
    seeGoldPerks(){
      let goldOffers = document.querySelector('#goldOff')
      let arrow = document.querySelector('.gold .down-arrow')

      goldOffers.classList.toggle('flexo')
      arrow.classList.toggle("rotate")
    },
    seeSilverPerks(){
      let silverOffers = document.querySelector('#silverOff')
      let arrow = document.querySelector('.silver .down-arrow')

      silverOffers.classList.toggle("flexo")
      arrow.classList.toggle("rotate")
    },
    handlePrice(value){
      if (value === 0){
        this.price = "32.500,15"
      } else if(value === 550){
        this.price = "33.050,15"
      } else{
        this.price = "32.750,15"
      }
    },
    handleContent(){
        let cardInput = document.querySelector('.card-number')
        let regex = /[^0-9]/gi
        if(cardInput.value.match(regex)){
          this.warning = "Introduzca sólo números"
        } else{
          this.warning = ""
        }
    }
   },
}
</script>


